*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Work Sans', sans-serif;
}

button{
  outline: none;
  background-color: inherit;
  border: none;
  color: #f8fafc;
  font-size: 1.2rem;
  margin: 3px auto;
  cursor: pointer;
}

.App
{
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.container{
  position: relative;
  background-color: #0284c7;
  color: #f8fafc;
  margin: 0.5rem 0;
  padding: 0.5rem 1rem;
  max-width: 90vw;
  text-align: center;
  display: flex;
  justify-content: center;
  border-radius: 13px;
  flex-direction: column;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
}

