.top-content{
  display: flex;
  justify-content: space-evenly;
  margin: 20px 0;
  font-weight: 500;
  font-size: 1.4rem;
  flex-wrap: wrap;
  gap: 1rem;
}

.dropdown{
  display:none;
  transform: rotate(90deg);
  font-size: 2rem;

}

.input-container{
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}

.input-text{
  display: flex;
  justify-content:center;
  align-items: center;
  font-weight: 800;
}

.input-text > input::placeholder{
  font-weight: 400;
}

.input-text input{
  outline: none;
  padding: 0.5rem 1rem;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  width:150%;
  color: black;
  border: none;

}

.temp-format{
  display: flex;
  align-items: center;
  justify-content: center;
}

.time-location{
  margin-top: 1.5rem;
  margin-bottom: 0.5rem;
  font-size: 1.2rem;
}

.city-country{
  font-size: 1.3rem;
  margin-bottom: 0.9rem;
}

.temp-container{
  display: flex;
  flex-direction: column;
}

.desc{
  font-size: 1.5rem;
  color: #6ee7b7;
  font-weight: 600;
}

.temp{
  margin: 1rem 3rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}

.temp > p{
  font-size: 1.6rem;
  font-weight: 600;
}

.more-details{
  display: flex;
  flex-direction: column;
  gap: 5px;
  font-size: 1.1rem;
}

.detail{
  margin: 0;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
}

.temp-more{
  /* style={{display: "flex", justifyContent: "center", alignItems: "center", gap: "3px", fontSize: "1.1rem"}} */
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3px;
  font-size: 1rem;
  flex-wrap: wrap;
}

.temp-more > .temp-more-box >  p > span{
  font-weight: 700;
}

.more-details > .detail > span{
  font-weight: 700;
}

.temp-more-box{
  display: flex;
}

.forecast-container{
  display: flex;
  flex-direction: column;
  margin-top: 1rem;
  margin-bottom: 0.5rem;
  margin-left: 1rem;
  margin-right: 1rem;
  justify-content: flex-start;
  gap: 5px;
}

hr{
  height: 2px;
  border-width: 0;
  background-color: #f8fafc;
}

.forecast-title{
  display: flex;
  font-size: 1.3rem;
  font-weight: 600;
}

.forecast-details{
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.forecast-box > p{
  font-size: 1.1rem;
}


.hidden{
  display: none;
}

@media (max-width: 1200px){
  .top-content{
